import React, {useState} from 'react'
import pro1 from '../image/Capture.PNG'

function Portfolio() {
    function project1_modal(){
        document.getElementById("modal").style.pointerEvents = "all"
        document.getElementById("modal").style.opacity = 1
    }

    function closeModal1(){
        document.getElementById("modal").style.pointerEvents = "none"
        document.getElementById("modal").style.opacity = 0
    }

    function go_right1(){
        if (!document.querySelectorAll(".carousel")[0].style.transform || document.querySelectorAll(".carousel")[0].style.transform == "translateX(0%)"){
            return document.querySelectorAll(".carousel")[0].style.transform = "translateX(-100%)"
        }
        if (document.querySelectorAll(".carousel")[0].style.transform == "translateX(-100%)"){
            return document.querySelectorAll(".carousel")[0].style.transform = "translateX(-200%)"
        }
        if (document.querySelectorAll(".carousel")[0].style.transform == "translateX(-200%)"){
            return document.querySelectorAll(".carousel")[0].style.transform = "translateX(-300%)"
        }
    }

    function go_left1(){
        if (!document.querySelectorAll(".carousel")[0].style.transform || document.querySelectorAll(".carousel")[0].style.transform == "translateX(0%)"){
            return
        }
        if (document.querySelectorAll(".carousel")[0].style.transform == "translateX(-100%)"){
            return document.querySelectorAll(".carousel")[0].style.transform = "translateX(0%)"
        }
        if (document.querySelectorAll(".carousel")[0].style.transform == "translateX(-200%)"){
            return document.querySelectorAll(".carousel")[0].style.transform = "translateX(-100%)"
        }
        if (document.querySelectorAll(".carousel")[0].style.transform == "translateX(-300%)"){
            return document.querySelectorAll(".carousel")[0].style.transform = "translateX(-200%)"
        }
    }

    function project2_modal(){
        document.getElementById(`modal2`).style.pointerEvents = "all"
        document.getElementById(`modal2`).style.opacity = 1
    }

    function closeModal2(){
        document.getElementById("modal2").style.pointerEvents = "none"
        document.getElementById("modal2").style.opacity = 0

    }

    function go_right2(){
        if (!document.querySelectorAll(".carousel")[1].style.transform || document.querySelectorAll(".carousel")[1].style.transform == "translateX(0%)"){
            return document.querySelectorAll(".carousel")[1].style.transform = "translateX(-100%)"
        }
        if (document.querySelectorAll(".carousel")[1].style.transform == "translateX(-100%)"){
            return document.querySelectorAll(".carousel")[1].style.transform = "translateX(-200%)"
        }
    }

    function go_left2(){
        if (!document.querySelectorAll(".carousel")[1].style.transform || document.querySelectorAll(".carousel")[1].style.transform == "translateX(0%)"){
            return
        }
        if (document.querySelectorAll(".carousel")[1].style.transform == "translateX(-100%)"){
            return document.querySelectorAll(".carousel")[1].style.transform = "translateX(0%)"
        }
        if (document.querySelectorAll(".carousel")[1].style.transform == "translateX(-200%)"){
            return document.querySelectorAll(".carousel")[1].style.transform = "translateX(-100%)"
        }
        if (document.querySelectorAll(".carousel")[1].style.transform == "translateX(-300%)"){
            return document.querySelectorAll(".carousel")[1].style.transform = "translateX(-200%)"
        }
    }



    return (
        <div className="portfolio" id="portfolio">
            <h1>Projects</h1>
            {
                <div className="modal" id="modal">
                    <div className="modal_content">
                        <div className="close_modal" onClick={closeModal1} id={1}>x</div>
                        <div className="inner_content">
                            <div>
                                <div className="carousel">
                                    <div></div>
                                    <div></div>
                                    <div></div>
                                    <div></div>
                                </div>
                            <div className="left"  onClick={go_left1}><i class="fas fa-arrow-left"></i></div>
                            <div className="right" onClick={go_right1}><i class="fas fa-arrow-right"></i></div>
                            </div>
                            <div className="modal_content_details">
                            <div>SaiKang Warrior (Dec 2020)</div>
                            <p>Saikang Warrior is a cross platform where users can find services
                            specifically to their needs and earn money for completing other's
                            requests.</p>
                            <a href="https://github.com/neronexius/SaikangWarrior" target="_blank"><i class="fab fa-github"></i></a>
                            </div>
                        </div>
                    </div>
                </div>
            }
            {
                <div className="modal" id="modal2">
                    <div className="modal_content2">
                        <div className="close_modal" onClick={closeModal2} id={1}>x</div>
                        <div className="inner_content">
                            <div>
                                <div className="carousel">
                                    <div></div>
                                    <div></div>
                                    <div></div>
                                </div>
                            <div className="left"  onClick={go_left2}><i class="fas fa-arrow-left"></i></div>
                            <div className="right" onClick={go_right2}><i class="fas fa-arrow-right"></i></div>
                            </div>
                            <div className="modal_content_details">
                            <div>Apt-Appt-App (Nov 2020)</div>
                            <p>Apt-Appt-App makes setting appointment/meeting/meetups easier with
                        friends or colleague. With the technology that users can block off
                        their unavaliable dates creates clarity for host to set dates.</p>
                        
                        </div>
                        </div>
                    </div>
                </div>
            }


            <div className="project_container">
                
                <div className="carousel_container1">
                    <div className="carousel">
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                    </div>
                    <div className="cover"></div>
                    <div className="proj_title">SaiKangWarrior</div>
                    <div className="modal_opener" onClick={project1_modal}><div>Learn More</div></div>
                    
                </div>
                <div className="carousel_container2">
                    <div className="carousel">
                        <div></div>
                        <div></div>
                        <div></div>
                    </div>
                    <div className="cover"></div>
                    <div className="proj_title">Apt-Appt-App</div>
                    <div className="modal_opener" onClick={project2_modal}><div>Learn More</div></div>
                </div>
                </div>
        </div>
    )
}

export default Portfolio
