import React from 'react'

function Nav() {
    function toPort(){
        let x = document.getElementById("portfolio")
        x.scrollIntoView({behavior:"smooth",block:"start"})
    }
    function toIntro(){
        let x = document.getElementById("intro")
        x.scrollIntoView({behavior:"smooth",block:"start"})
    }
    function toAbout(){
        let x = document.getElementById("about")
        x.scrollIntoView({behavior:"smooth",block:"start"})
    }
    function toContact(){
        let x = document.getElementById("contact")
        x.scrollIntoView({behavior:"smooth",block:"start"})
    }
    return (
        <div className="navbar">
            <div className="nav_container"> 
                <div onClick={toIntro}>Home</div> 
                <div onClick={toAbout}>About</div> 
                <div onClick={toPort}>Portfolio</div> 
                <div onClick={toContact}>Contact</div> 
            </div>
        </div>
    )
}

export default Nav
